import React from 'react'
import {withAuth} from 'react-router-auth-provider'

function Hello({authInfo, onLogout}){
  return (
    <div>
      Hello {authInfo.username}
      <button variant="raised" onClick={()=>onLogout()}>
        Logout
      </button>
    </div>)
}

export default withAuth(Hello)




